import {calculateBriefBudget, calculateBufferedBudget, Campaign} from '../models/campaign';
import {AdRun, calculateNetNet, calculateClientNet} from '../models/adrun';
import {Component, EventEmitter, Input, OnInit, OnChanges, Output} from '@angular/core';
import {BudgetType} from '../models/BudgetType';
import {ExchangeCurrency} from '../models/exchange-currency';

@Component({
  selector: 'md-ad-runs-header',
  templateUrl: './ad-runs-header.html',
  styleUrls: ['./ad-runs-header.css']
})
export class AdRunsHeaderComponent implements OnInit, OnChanges {
  @Input() campaign: Campaign;
  @Input() adRuns: AdRun[];

  @Output() editCampaign = new EventEmitter<string>();

  calculatedFields: AdRunsHeaderCalculatedFields;

  ngOnInit() {
    this.reCalculateFields();
  }

  ngOnChanges() {
    this.reCalculateFields();
  }

  reCalculateFields() {
    const bufferedBudget = calculateBufferedBudget(this.campaign.budget, this.campaign.buffer);
    const briefBudget = this.campaign.exchangeCurrency === ExchangeCurrency.HUF ?
      bufferedBudget :
      calculateBriefBudget(bufferedBudget, this.campaign.exchangeRate);
    const plannedBudget = this.calculatePlannedBudget();
    this.calculatedFields = {
      briefBudget,
      plannedBudget,
      remainingBudget: briefBudget - plannedBudget
    };
  }

  calculatePlannedBudget(): number {
    return this.adRuns.reduce((acc, adRun) => {
      let adRunCost = 0;
      switch (this.campaign.budgetType) {
        case BudgetType.NetNet:
          adRunCost = calculateNetNet(adRun);
          break;
        default:
          adRunCost = calculateClientNet(adRun, this.campaign);
          break;
      }
      return acc + adRunCost;
    }, 0);
  }

  goToCampaignEditingForm() {
    this.editCampaign.emit(this.campaign.identifier);
  }
}

interface AdRunsHeaderCalculatedFields {
  briefBudget: number;
  plannedBudget: number;
  remainingBudget: number;
}



