import {ExtraColumn} from './extra-column';
import {ExchangeCurrency} from './exchange-currency';
import {BudgetType} from './BudgetType';
import {convertPercentageToIncreasingMultiplier} from '../shared/math-utils';
import BigNumber from 'bignumber.js';
import * as _ from 'lodash';

export interface Campaign {
  identifier: string;
  client: string;
  brand?: string;
  campaign: string;
  startDate: string;
  endDate: string;
  target?: string;
  planner?: string;
  fee?: number;
  exchangeCurrency?: ExchangeCurrency;
  exchangeRate?: number;
  buffer?: number;
  budget?: number;
  budgetType?: BudgetType;
  extraColumns?: ExtraColumn[];
}

/**
 * Buffered budget
 *
 * A campaign's budget extended with a buffer (in case a foreign currency's exchange rate gets higher)
 */
export function calculateBufferedBudget(budget: number, buffer: number): number {
  if (_.isNil(budget)) {
    throw new Error(`Invalid value for 'budget', got: ${budget}`);
  }
  if (_.isNil(buffer)) {
    throw new Error(`Invalid value for 'buffer', got: ${buffer}`);
  }

  const actualBudget = new BigNumber(budget);
  const bufferMultiplier = convertPercentageToIncreasingMultiplier(buffer);

  return actualBudget.mul(bufferMultiplier).toNumber();
}

/**
 * Brief budget
 *
 * A campaign's buffered budget multiplied by the currency's exchange rate
 */
export function calculateBriefBudget(bufferedBudget: number, exchangeRate: number): number {
  if (_.isNil(bufferedBudget)) {
    throw new Error(`Invalid value for 'bufferedBudget', got: ${bufferedBudget}`);
  }
  if (_.isNil(exchangeRate)) {
    throw new Error(`Invalid value for 'exchangeRate', got: ${exchangeRate}`);
  }

  const budget = new BigNumber(bufferedBudget);
  const rate = new BigNumber(exchangeRate);

  return budget.mul(rate).toNumber();
}



