import {DECIMAL_LIMIT, THOUSANDS_SEPARATOR_SYMBOL} from '../shared/constants';
import {calculateBriefBudget, calculateBufferedBudget, Campaign} from '../models/campaign';
import {Component, Output, EventEmitter, OnDestroy, Input, OnInit} from '@angular/core';
import { FormBuilder, Validators, FormGroup, FormControl } from '@angular/forms';
import {NgbDateParserFormatter, NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
import { UnmaskingService } from '../services/unmasking';
import createNumberMask from 'text-mask-addons/dist/createNumberMask';
import {BudgetType} from '../models/BudgetType';
import {ExchangeCurrency} from '../models/exchange-currency';
import {Subject} from 'rxjs/Subject';
import {combineLatest} from 'rxjs/observable/combineLatest';
import {startWith, takeUntil} from 'rxjs/operators';
import { v4 as uuid } from 'uuid';
import {clients as listOfClients} from './clients';
import * as _ from 'lodash';

@Component({
  selector: 'md-new-campaign-form',
  templateUrl: './new-campaign-form.html',
  styles: [`
    .md-next-button {
      font-size: 24px;
    }

    .table th,
    .table td {
      padding: 0.25rem 0.75rem;
    }

    [role=button] {
      cursor:pointer;
    }
  `]
})
export class NewCampaignFormComponent implements OnInit, OnDestroy {
  @Input() campaign: Campaign;

  @Output() formSubmission = new EventEmitter<Campaign>();

  readonly exchangeCurrencies = ExchangeCurrency;
  readonly budgetTypes = BudgetType;
  readonly clients = listOfClients;
  readonly DEFAULT_BUFFER = 10;
  readonly NEW_CAMPAIGN_HEADER = 'New Campaign';
  readonly EDIT_CAMPAIGN_HEADER = 'Edit Campaign';
  readonly BUDGET_MASK = createNumberMask({
    prefix: '',
    thousandsSeparatorSymbol: THOUSANDS_SEPARATOR_SYMBOL,
    allowDecimal: true,
    decimalLimit: DECIMAL_LIMIT
  });

  headerText = this.NEW_CAMPAIGN_HEADER;
  clientClasses = {
    basic: 'form-control',
    warning: 'form-control border-warning',
    danger: 'form-control border-danger'
  };
  clientInputClass = this.clientClasses.basic;
  hasOldStartDate: boolean;
  hasUnknownClient: boolean;
  newCampaignForm: FormGroup;
  calculatedFields = {};
  // FIXME: this will be set from a configuration variable instead of a fixed constant
  localCurrency = ExchangeCurrency.HUF;

  private unsubscribe: Subject<any> = new Subject();

  constructor(
    private formBuilder: FormBuilder,
    private dateFormatter: NgbDateParserFormatter,
    private unmaskingService: UnmaskingService
  ) { }

  ngOnInit() {
    this.headerText = this.campaign ? this.EDIT_CAMPAIGN_HEADER : this.NEW_CAMPAIGN_HEADER;

    this.createForm(this.campaign);
    this.disableExchangeRateForDomicileCurrency();
    this.recalculateBriefBudget();
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  createForm(campaign: Campaign) {
    this.newCampaignForm = this.formBuilder.group({
      identifier: campaign && campaign.identifier || uuid(),
      client: [campaign && campaign.client || '', Validators.required],
      brand: [campaign && campaign.brand || null, Validators.required],
      campaign: [campaign && campaign.campaign || null, Validators.required],
      startDate: [campaign && this.dateFormatter.parse(campaign.startDate) || null, Validators.required],
      endDate: [campaign && this.dateFormatter.parse(campaign.endDate) || null, Validators.required],
      target: [campaign && campaign.target || null, Validators.required],
      planner: [campaign && campaign.planner || null, Validators.required],
      fee: [campaign && campaign.fee || null, [Validators.required, Validators.min(0), Validators.max(100)]],
      exchangeCurrency: campaign && campaign.exchangeCurrency || this.localCurrency,
      exchangeRate: [campaign && campaign.exchangeRate || null, Validators.required],
      buffer: [campaign && campaign.buffer || this.DEFAULT_BUFFER, Validators.required],
      budget: [campaign && campaign.budget || null, Validators.required],
      budgetType: [campaign && campaign.budgetType || '', Validators.required]
    }, {
      validator: this.dateValidator('startDate', 'endDate')
    });
  }

  disableExchangeRateForDomicileCurrency() {
    this.newCampaignForm.get('exchangeCurrency').valueChanges
      .pipe(
        startWith(this.newCampaignForm.get('exchangeCurrency').value),
        takeUntil(this.unsubscribe)
      )
      .subscribe(currency => {
        // FIXME: exchangeCurrency and exchangeRate should not be specifically linked to HUF
        currency === ExchangeCurrency.HUF ?
          this.newCampaignForm.get('exchangeRate').disable() :
          this.newCampaignForm.get('exchangeRate').enable();
      });
  }

  recalculateBriefBudget() {
    combineLatest(
      this.newCampaignForm.get('budget').valueChanges,
      this.newCampaignForm.get('buffer').valueChanges.pipe(startWith(this.DEFAULT_BUFFER)),
      this.newCampaignForm.get('exchangeRate').valueChanges
    ).pipe(takeUntil(this.unsubscribe))
      .subscribe(([budget, buffer, exchangeRate]) => {
        budget = this.unmaskingService.unmask(budget);

        if (_.isNil(budget) || _.isNil(buffer) || _.isNil(exchangeRate)) {
          this.calculatedFields = {
            ...this.calculatedFields,
            briefBudget: 0
          };
          return;
        }

        const bufferedBudget = calculateBufferedBudget(budget, buffer);

        this.calculatedFields = {
          ...this.calculatedFields,
          briefBudget: calculateBriefBudget(bufferedBudget, exchangeRate)
        };
      });
  }

  validateAndSubmitForm() {
    if (this.newCampaignForm.valid) {
      const campaign = this.newCampaignForm.value;

      campaign.startDate = this.dateFormatter.format(campaign.startDate);
      campaign.endDate = this.dateFormatter.format(campaign.endDate);
      campaign.budget = this.unmaskingService.unmask(campaign.budget);
      campaign.exchangeRate = campaign.exchangeCurrency === ExchangeCurrency.HUF ? 1 : campaign.exchangeRate;
      campaign.buffer = campaign.exchangeCurrency === ExchangeCurrency.HUF ? 0 : campaign.buffer;

      this.formSubmission.emit(campaign);
    }
    else {
      this.validateAllFields(this.newCampaignForm);
    }
  }

  validateAllFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);

      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      }
      else if (control instanceof FormGroup) {
        this.validateAllFields(control);
      }
    });
  }

  validateStartDate(date: NgbDateStruct) {
    const startDate = new Date(date.year, date.month - 1, date.day);
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    this.hasOldStartDate = startDate < today;
  }

  validateClient() {
    const client = this.newCampaignForm.get('client').value;

    if (_.isNil(client)) {
      this.clientInputClass = this.clientClasses.danger;
      return;
    }

    this.hasUnknownClient = !this.clients.includes(client);
    this.clientInputClass = this.hasUnknownClient ? this.clientClasses.warning : this.clientClasses.basic;
  }

  dateValidator(start: string, end: string) {
    return (group: FormGroup): { [key: string]: any } => {
      const startDateNgb = group.controls[start].value;
      const endDateNgb = group.controls[end].value;
      const startDate = this.dateFormatter.format(startDateNgb);
      const endDate = this.dateFormatter.format(endDateNgb);
      if (startDate > endDate) {
        return {
          dates: 'End date should be later than start date.'
        };
      }
      return {};
    };
  }

  currencyIsLocal(currency: string) {
    return currency === this.localCurrency.toString();
  }
}



