import { Component, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {AuthenticationService} from '../services/authentication.service';

@Component({
  selector: 'md-login-form',
  template: `
	<ul class="bg-slideshow">
		<li><span>image 1</span><div>&nbsp;</div></li>
		<li><span>image 2</span><div>&nbsp;</div></li>
		<li><span>image 3</span><div>&nbsp;</div></li>
	</ul>
	<div class="container pt-5">
		<div class="row">
			<div class="col-md-12">
				<div class="row">
					<div class="col-md-6 mx-auto">
						<span class="anchor" id="formLogin"></span>
						<div class="card rounded-0">
							<div class="card-header">
								<h3 class="mb-0">Login</h3>
							</div>
							<div class="card-body">
								<div *ngIf="errorMessage" class="alert alert-danger">
									{{ errorMessage }}
								</div>
								<form class="form" role="form" autocomplete="off" id="formLogin" (ngSubmit)="f.form.valid && login()" #f="ngForm" novalidate>
									<div class="form-group">
										<label for="username">Username</label>
										<input type="text" class="form-control form-control-lg rounded-0" name="username" id="username" required="" [(ngModel)]="model.username" #username="ngModel" [ngClass]="{ 'is-invalid': f.submitted && !username.valid }" autofocus>
										<div *ngIf="f.submitted && !username.valid" class="invalid-feedback">Username is required</div>
									</div>
									<div class="form-group">
										<label>Password</label>
										<input type="password" class="form-control form-control-lg rounded-0" name="password" required="" autocomplete="new-password" [(ngModel)]="model.password" #password="ngModel" [ngClass]="{ 'is-invalid': f.submitted && !password.valid }">
										<div *ngIf="f.submitted && !password.valid" class="invalid-feedback">Password is required</div>
									</div>
									<button class="btn btn-success btn-lg float-right" type="submit">Login</button>
								</form>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
  `,
  styleUrls: ['./login-form.css']
})
export class LoginFormComponent {
	model: any = {username: 'admin', password: 'admin'};
	errorMessage: string;

	constructor(
		private router: Router,
		private authenticationService: AuthenticationService
	) {}

	login() {
		if (this.authenticationService.authenticate(this.model.username, this.model.password)) {
      // FIXME: routing hack
      this.router.navigate(["campaigns"]);
		} else {
			this.errorMessage = "Invalid username or password (try admin/admin)";
		}
	}
}



