import { Campaign } from '../models/campaign';
import { SelectCampaignAdRuns } from '../actions/ad-runs';
import { map, switchMap } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { Action } from '@ngrx/store';
import { Actions, Effect } from '@ngrx/effects';
import { Router } from '@angular/router';
import {
  ADD_CAMPAIGN,
  AddCampaign,
  UPDATE_CAMPAIGN,
  UpdateCampaign,
  EDIT_CAMPAIGN,
  EditCampaign
} from '../actions/campaigns';
import { SELECT_CAMPAIGN_ADRUNS } from '../actions/ad-runs';

// FIXME: use some general error handling for the effects
@Injectable()
export class CampaignsEffects {

  @Effect()
  redirectOnNewCampaign: Observable<Action> = this.actions.ofType(ADD_CAMPAIGN).pipe(
    map((action: AddCampaign): AddCampaignEffectPayload => {
      return {
        campaign: action.payload,
        isEditingMode: true
      };
    }),
    switchMap((payload: AddCampaignEffectPayload) => {
      return of(new SelectCampaignAdRuns(payload.campaign, payload.isEditingMode));
    })
  );

  @Effect({dispatch: false})
  redirectOnCampaignSelection: Observable<Action> = this.actions.ofType(SELECT_CAMPAIGN_ADRUNS).pipe(
    switchMap((action: SelectCampaignAdRuns) => {
      const isEditingMode = action.isEditingMode;
      const navigationParams = isEditingMode ? {isEditingMode} : {};

      this.router.navigate(['adRuns', navigationParams]);

      return of({type: 'NO_ACTION'});
    })
  );

  @Effect({dispatch: false})
  redirectToCampaignEditingForm: Observable<Action> = this.actions.ofType(EDIT_CAMPAIGN).pipe(
    switchMap((action: EditCampaign) => {
      const campaignId = action.payload;
      const navigationParams = campaignId ? {campaignId} : {};

      this.router.navigate(['new-campaign', navigationParams]);

      return of({type: 'NO_ACTION'});
    })
  );

  @Effect()
  redirectOnEditedCampaign: Observable<Action> = this.actions.ofType(UPDATE_CAMPAIGN).pipe(
    map((action: UpdateCampaign) => action.payload),
    switchMap((payload: Campaign) => {
      return of(new SelectCampaignAdRuns(payload));
    })
  );

  constructor(
    private actions: Actions,
    private router: Router
  ) {
  }

}

interface AddCampaignEffectPayload {
  campaign: Campaign;
  isEditingMode: boolean;
}



