import { AdRunsHelperService } from './services/ad-runs-helper';
import { CampaignsEffects } from './effects/campaigns';
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {ReactiveFormsModule} from '@angular/forms';
import {RouterModule} from '@angular/router';
import {StoreModule} from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import {reducers} from './reducers/index';
import {OverviewComponent} from './containers/overview';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {CampaignsComponent} from './components/campaigns';
import {CampaignsSearchComponent} from './components/campaigns-search';
import {NewCampaignComponent} from './containers/new-campaign';
import {NewCampaignFormComponent} from './components/new-campaign-form';
import {AdRunsPageComponent} from './containers/ad-runs-page';
import {AdRunsHeaderComponent} from './components/ad-runs-header';
import {AdRunsComponent} from './components/ad-runs';
import {AdRunsFormComponent} from './components/ad-runs-form';
import {AdRunsStatisticsComponent} from './components/ad-runs-statistics';
import {FormsModule} from '@angular/forms';
import { DragulaModule } from 'ng2-dragula';
import {Ng2CompleterModule} from 'ng2-completer';
import {UnmaskingService} from './services/unmasking';
import { TextMaskModule } from 'angular2-text-mask';
import {FormattedNumberPipe} from './pipes/formatted-number.pipe';
import {MultiselectDropdownModule} from 'angular-2-dropdown-multiselect';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    RouterModule,
    FormsModule,
    RouterModule.forChild([
      {
        path: 'campaigns',
        component: OverviewComponent,
      },
      {
        path: 'new-campaign',
        component: NewCampaignComponent
      },
      {
        path: 'adRuns',
        component: AdRunsPageComponent
      }
    ]),
    /**
     * StoreModule.forFeature is used for composing state from feature modules. These modules can be loaded
     * eagerly or lazily and will be dynamically added to the existing state.
     */
    StoreModule.forFeature('campaigns', reducers),
    EffectsModule.forFeature([CampaignsEffects]),
    NgbModule,
    DragulaModule,
    Ng2CompleterModule,
    TextMaskModule,
    MultiselectDropdownModule
  ],
  declarations: [
    OverviewComponent,
    CampaignsComponent,
    CampaignsSearchComponent,
    NewCampaignComponent,
    NewCampaignFormComponent,
    AdRunsPageComponent,
    AdRunsHeaderComponent,
    AdRunsComponent,
    AdRunsFormComponent,
    AdRunsStatisticsComponent,
    FormattedNumberPipe
  ],
  providers: [
    UnmaskingService,
    AdRunsHelperService
  ]
})
export class CampaignsModule {
  static forRoot() {
    return {
      ngModule: CampaignsModule
    };
  }
}



