import {Component, EventEmitter, Input, Output} from '@angular/core';
import { Campaign } from '../models/campaign';

@Component({
  selector: 'md-campaigns',
  template: `
    <div class="row justify-content-center p-2">
      <h1>
        Campaigns
      </h1>
    </div>

    <div class="row justify-content-center p-2">
      <table class="table table-hover md-recent-campaigns ">
        <thead class="thead-light">
        <tr>
          <th colspan="3">
            <md-campaigns-search (search)="search.emit($event)"></md-campaigns-search>
          </th>
        </tr>
        </thead>
        <tbody>

          <tr *ngFor="let campaign of campaigns" (click)="selected.emit(campaign)">
            <td class="text-uppercase">
              {{campaign.client}} - {{campaign.campaign}}
            </td>
            <td>
              {{campaign.identifier}}
            </td>
            <td>
              {{campaign.startDate}} - {{campaign.endDate}}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <a>
      <div class="row justify-content-center p-1">
        <button type="button" class="btn btn-primary btn-lg btn-block md-recent-campaign"
			[routerLink]="['new-campaign/']">
          New Campaign
        </button>
      </div>
    </a>
  `,
  styleUrls: ['./campaigns.css']
})
export class CampaignsComponent {

  @Input() campaigns: Array<Campaign>;

  @Output() selected: EventEmitter<Campaign> = new EventEmitter();

  @Output() search: EventEmitter<string> = new EventEmitter();


}



