import * as adRuns from '../actions/ad-runs';
import {Campaign} from '../models/campaign';
import {AdRun} from '../models/adrun';
import {AddAdRun, DeleteAdRun, EditAdRun, ReorderAdRuns, SelectCampaignAdRuns} from '../actions/ad-runs';
import {PricingType} from '../models/pricing-type';
import {AdServingType} from '../models/ad-serving-type';


export interface State {
  campaign: Campaign;
  campaignAdRuns: Array<AdRun>;
}

const initialState: State = {
  campaign: null,
  campaignAdRuns: []
};


export function reducer(state = initialState, action: adRuns.Actions): State {
  switch (action.type) {
    case adRuns.SELECT_CAMPAIGN_ADRUNS:
      return <State>{
        ...state,
        campaign: (<SelectCampaignAdRuns>action).campaign
      };

    case adRuns.LOAD_ADRUNS:
      const campaignAdRuns = loadAdRuns(state.campaign.identifier);
      return <State>{
        ...state,
        campaignAdRuns
      };


    case adRuns.ADD_ADRUN:
      saveAdRun(state.campaign.identifier, (<AddAdRun>action).payload);

      return <State>{
        ...state,
        campaignAdRuns: loadAdRuns(state.campaign.identifier)
      };

    case adRuns.EDIT_ADRUN:
      updateAdRun(state.campaign.identifier, (<EditAdRun>action).payload);

      return <State>{
        ...state,
        campaignAdRuns: loadAdRuns(state.campaign.identifier)
      };

    case adRuns.DELETE_ADRUN:
      deleteAdRun(state.campaign.identifier, (<DeleteAdRun>action).payload);

      return <State>{
        ...state,
        campaignAdRuns: loadAdRuns(state.campaign.identifier)
      };

    case adRuns.REORDER_ADRUNS:
      mockDB[0].campaignAdRuns = (<ReorderAdRuns>action).payload;

      return <State>{
        ...state,
        campaignAdRuns: (<ReorderAdRuns>action).payload
      };

    default:
      return state;
  }
}

// ---- MOCK SECTION ----

let mockID = 0;
function generateId() {
  const id = mockID;
  mockID = id + 1;
  return id;
}

interface MockDbEntry {
  identifier: string;
  campaignAdRuns: Array<AdRun>;
}

function findCampaginEntry(campaignIdentifier: string) {
  const result = mockDB.filter(entry => entry.identifier === campaignIdentifier);
  if (!result || result.length === 0) {
    return null;
  }

  // make sure that there is only a single entry in the mockDB
  return result[0];
}

function loadAdRuns(campaignIdentifier: string) {
  if (!campaignIdentifier) {
    console.warn(`Invalid campaign identifier: ${campaignIdentifier}`);
    return [];
  }

  const result = mockDB.filter(entry => entry.identifier === campaignIdentifier);
  if (!result || result.length === 0) {
    return [];
  }

  // make sure that there is only a single entry in the mockDB
  return result[0].campaignAdRuns;
}

function saveAdRun(campaignIdentifier: string, adRun: AdRun) {
  let entry = findCampaginEntry(campaignIdentifier);
  if (!entry) { // not in the mockdb yet, we add it
    mockDB = [...mockDB, {
      identifier: campaignIdentifier,
      campaignAdRuns: []
    }];
    entry = findCampaginEntry(campaignIdentifier);
  }

  const adRunWithId = <AdRun>{
    ...adRun,
    id: generateId()
  };

  entry.campaignAdRuns = [...entry.campaignAdRuns, adRunWithId];
}

function updateAdRun(identifier: string, editedAdRun: AdRun) {
  const entry = findCampaginEntry(identifier);
  // we do the update this way to ensure that the order is preserved
  const newAdRunEntries = entry.campaignAdRuns.map(adRun => {
    if (adRun.id === editedAdRun.id) {
      return editedAdRun;
    } else {
      return { ...adRun };
    }
  });

  entry.campaignAdRuns = newAdRunEntries;
}

function deleteAdRun(identifier: string, editedAdRun: AdRun) {
  const entry = findCampaginEntry(identifier);
  // we do the update this way to ensure that the order is preserved
  const newAdRunEntries = entry.campaignAdRuns.filter(adRun => adRun.id !== editedAdRun.id);

  entry.campaignAdRuns = newAdRunEntries;
}

