import * as fromRoot from '../../reducers';
import { Store } from '@ngrx/store';
import {Component, OnDestroy} from '@angular/core';
import {AddCampaign, UpdateCampaign} from '../actions/campaigns';
import {Campaign} from '../models/campaign';
import {first} from 'rxjs/operators';
import {getCampaigns} from '../reducers/index';
import {ActivatedRoute} from '@angular/router';
import {Subject} from 'rxjs/Subject';
import {combineLatest} from 'rxjs/observable/combineLatest';

@Component({
  selector: 'md-new-campaign',
  template: `
    <md-new-campaign-form
        [campaign]="campaignToEdit"
        (formSubmission)="formSubmission($event)"
    >
    </md-new-campaign-form>
  `
})
export class NewCampaignComponent implements OnDestroy {
  private unsubscribe: Subject<any> = new Subject();
  private campaignToEdit: Campaign;

  constructor(
    private store: Store<fromRoot.State>,
    private route: ActivatedRoute
  ) {
    combineLatest(
      this.route.paramMap,
      this.store.select(getCampaigns)
    )
      .pipe(first())
      .subscribe(([params, campaigns]) => {
        const campaignId = params.get('campaignId');
        this.campaignToEdit = campaigns.find(campaign => campaign.identifier === campaignId);
      });
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  formSubmission(submittedCampaign: Campaign) {
    this.store.select(getCampaigns)
      .pipe(first())
      .subscribe(campaigns => {
        campaigns.find(campaign => campaign.identifier === submittedCampaign.identifier) ?
          this.updateCampaign(submittedCampaign) :
          this.newCampaign(submittedCampaign);
      });
  }

  newCampaign(campaign: Campaign) {
    this.store.dispatch(new AddCampaign(campaign));
  }

  updateCampaign(campaign: Campaign) {
    this.store.dispatch(new UpdateCampaign(campaign));
  }
}



