import { ReorderAdRuns } from '../actions/ad-runs';
import {Component} from '@angular/core';
import {AdRun} from '../models/adrun';
import {Store} from '@ngrx/store';
import * as fromRoot from '../../reducers';
import * as fromCampaign from '../reducers';
import {Campaign} from '../models/campaign';
import {AddAdRun, DeleteAdRun, EditAdRun, LoadAdRuns} from '../actions/ad-runs';
import {Subject} from 'rxjs/Subject';
import {Observable} from 'rxjs/Observable';
import {merge} from 'rxjs/operators';
import {EditCampaign} from '../actions/campaigns';

@Component({
  selector: 'md-ad-runs-page',
  template: `
    <md-ad-runs-header
      [campaign]="selectedCampaign | async"
      [adRuns]="adRunsStateChange | async"
      (editCampaign)="editCampaign($event)">
    </md-ad-runs-header>
    <md-ad-runs [entries]="adRunEntries | async"
                [entriesPlusForm]="adRunsStateChange | async"
                [campaign]="selectedCampaign | async"
                (deleteAdRun)="deleteAdRun($event)"
                (add)="addAdRun($event)"
                (edit)="editAdRun($event)"
                (adRunsChange)="adRunsChange($event)"
                (orderChanged)="reorderAdRuns($event)">
    </md-ad-runs>
    <md-ad-runs-statistics></md-ad-runs-statistics>
  `
})
export class AdRunsPageComponent {
  adRunEntries: Store<AdRun[]>;
  selectedCampaign: Store<Campaign>;
  adRunChange$: Subject<AdRun[]> = new Subject();
  adRunsStateChange: Observable<AdRun[]>;

  constructor(private store: Store<fromRoot.State>) {
    this.store.dispatch(new LoadAdRuns());
    this.selectedCampaign = this.store.select(fromCampaign.getSelectedCampaign);
    this.adRunEntries = this.store.select(fromCampaign.getAdRuns);
    this.adRunsStateChange = this.adRunEntries.pipe(merge(this.adRunChange$));
  }

  addAdRun(adRun: AdRun) {
    this.store.dispatch(new AddAdRun(adRun));
  }

  deleteAdRun(adRun: AdRun) {
    this.store.dispatch(new DeleteAdRun(adRun));
  }

  editAdRun(adRun: AdRun) {
    this.store.dispatch(new EditAdRun(adRun));
  }

  adRunsChange(adRuns: AdRun[]) {
    this.adRunChange$.next(adRuns);
  }

  reorderAdRuns(adRuns: AdRun[]) {
    this.store.dispatch(new ReorderAdRuns(adRuns));
  }

  editCampaign(campaignId: string) {
    this.store.dispatch(new EditCampaign(campaignId));
  }
}



