import { UnitsPerWeek } from './units-per-week';
import { Campaign } from './campaign';
import BigNumber from 'bignumber.js';
import * as _ from 'lodash';
import {
  convertPercentageToIncreasingMultiplier,
  convertPercentageToReducingMultiplier
} from '../shared/math-utils';
import {AdServingType} from './ad-serving-type';
import {PricingType} from './pricing-type';

const AV_MULTIPLIER = 1000;
const REACH_UU_MULTIPLIER = 1000;

export interface AdRun {
  id: number;
  salesHouse: string;
  site?: string;
  placement?: string;
  format?: string;
  duration?: string;
  date?: string;
  size?: string[];
  weeklyBreakdown?: UnitsPerWeek[];
  pricingType?: PricingType;
  units?: number;
  baseOfAdServing?: AdServingType;
  ratecard?: number;
  estimatedAV?: number;
  estimatedCT?: number;
  estimatedReachUU?: number;
  estimatedCTR?: number;
  estimatedLead?: number;
  estimatedAcquisition?: number;
  volumeDiscount?: number;
  otherDiscount?: number;
  agencyDiscount?: number;
  comment?: string;
}

/**
   * Total ratecard
   *
   * An AdRun's ratecard multiplied by its units
   */
export function calculateSumRatecard(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const ratecard = new BigNumber(adrun.ratecard);
  const units = new BigNumber(adrun.units);

  return ratecard.mul(units).toNumber();
}

/**
   * Net price
   *
   * An AdRun's total ratecard reduced by the volume discount and other discounts
   */
export function calculateNet(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const sumRatecard = new BigNumber(calculateSumRatecard(adrun));
  const volumeDiscountMultiplier = convertPercentageToReducingMultiplier(adrun.volumeDiscount);
  const otherDiscountsMultiplier = convertPercentageToReducingMultiplier(adrun.otherDiscount);

  return sumRatecard.mul(volumeDiscountMultiplier).mul(otherDiscountsMultiplier).toNumber();
}

/**
 * NetNet
 *
 * An AdRun's net price reduced by the agency discount
 */
export function calculateNetNet(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const net = new BigNumber(calculateNet(adrun));
  const agencyDiscountMultiplier = convertPercentageToReducingMultiplier(adrun.agencyDiscount);

  return net.mul(agencyDiscountMultiplier).toNumber();
}

/**
 * Client net
 *
 * An AdRun's NetNet increased by the campaign fee
 */
export function calculateClientNet(adrun: AdRun, campaign: Campaign) {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }
  if (_.isNil(campaign)) {
    throw new Error(`Invalid value for 'campaign', got: ${campaign}`);
  }

  const netNet = new BigNumber(calculateNetNet(adrun));
  const campaignFeeMultiplier = convertPercentageToIncreasingMultiplier(campaign.fee);

  return netNet.mul(campaignFeeMultiplier).toNumber();
}

/**
 * CPM
 *
 * An AdRun's NetNet divided by its estimated AV * AV_MULTIPLIER
 */
export function calculateCPM(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const estimatedAV = new BigNumber(adrun.estimatedAV);

  if (estimatedAV.equals(0)) {
    return 0;
  }

  const netNet = new BigNumber(calculateNetNet(adrun));

  return netNet.dividedBy(estimatedAV).mul(AV_MULTIPLIER).toNumber();
}

/**
 * CPC
 *
 * An AdRun's NetNet divided by its estimated CT
 */
export function calculateCPC(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const estimatedCT = new BigNumber(adrun.estimatedCT);

  if (estimatedCT.equals(0)) {
    return 0;
  }

  const netNet = new BigNumber(calculateNetNet(adrun));

  return netNet.dividedBy(estimatedCT).toNumber();
}

/**
 * CPT
 *
 * An AdRun's NetNet divided by its estimated reach UU * REACH_UU_MULTIPLIER
 */
export function calculateCPT(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const estimatedReachUU = new BigNumber(adrun.estimatedReachUU);

  if (estimatedReachUU.equals(0)) {
    return 0;
  }

  const netNet = new BigNumber(calculateNetNet(adrun));

  return netNet.dividedBy(estimatedReachUU).mul(REACH_UU_MULTIPLIER).toNumber();
}

/**
 * CPL
 *
 * An AdRun's NetNet divided by its estimated lead
 */
export function calculateCPL(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const estimatedLead = new BigNumber(adrun.estimatedLead);

  if (estimatedLead.equals(0)) {
    return 0;
  }

  const netNet = new BigNumber(calculateNetNet(adrun));

  return netNet.dividedBy(estimatedLead).toNumber();
}

/**
 * CPA
 *
 * An AdRun's NetNet divided by its estimated acquisition
 */
export function calculateCPA(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const estimatedAcquisition = new BigNumber(adrun.estimatedAcquisition);

  if (estimatedAcquisition.equals(0)) {
    return 0;
  }

  const netNet = new BigNumber(calculateNetNet(adrun));

  return netNet.dividedBy(estimatedAcquisition).toNumber();
}

/**
 * Estimated CTR
 * An AdRun's estimated CT divided by its estimated AV
 */
export function calculateEstimatedCTR(estimatedCT: number, estimatedAV: number): number {
  if (_.isNil(estimatedCT)) {
    throw new Error(`Invalid value for 'estimatedCT', got: ${estimatedCT}`);
  }
  if (_.isNil(estimatedAV)) {
    throw new Error(`Invalid value for 'estimatedAV', got: ${estimatedAV}`);
  }

  const bigEstimatedCT = new BigNumber(estimatedCT);
  const bigEstimatedAV = new BigNumber(estimatedAV);

  return bigEstimatedCT.dividedBy(bigEstimatedAV).toNumber();
}

/**
 * Estimated AV
 * An AdRun's estimated CT divided by its estimated CTR
 */
export function calculateEstimatedAV(estimatedCT: number, estimatedCTR: number): number {
  if (_.isNil(estimatedCT)) {
    throw new Error(`Invalid value for 'estimatedCT', got: ${estimatedCT}`);
  }
  if (_.isNil(estimatedCTR)) {
    throw new Error(`Invalid value for 'estimatedCTR', got: ${estimatedCTR}`);
  }

  const bigEstimatedCT = new BigNumber(estimatedCT);
  const bigEstimatedCTR = new BigNumber(estimatedCTR);

  return bigEstimatedCT.dividedBy(bigEstimatedCTR).toNumber();
}

/**
 * Estimated CT
 * An AdRun's estimated CTR multiplied by its estimated AV
 */
export function calculateEstimatedCT(estimatedCTR: number, estimatedAV: number): number {
  if (_.isNil(estimatedCTR)) {
    throw new Error(`Invalid value for 'estimatedCTR', got: ${estimatedCTR}`);
  }
  if (_.isNil(estimatedAV)) {
    throw new Error(`Invalid value for 'estimatedAV', got: ${estimatedAV}`);
  }

  const bigEstimatedCTR = new BigNumber(estimatedCTR);
  const bigEstimatedAV = new BigNumber(estimatedAV);

  return bigEstimatedCTR.mul(bigEstimatedAV).toNumber();
}

/**
   * Distributes units evenly across the weeks of an AdRun
   */
export function distributeUnitsEvenly(adrun: AdRun): UnitsPerWeek[] {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const allReceive = Math.floor(adrun.units / adrun.weeklyBreakdown.length);
  let remain = adrun.units - (allReceive * adrun.weeklyBreakdown.length);

  return adrun.weeklyBreakdown.map(week => {
    const newWeek = { ...week };

    remain -= 1;

    newWeek.units = allReceive + (remain < 0 ? 0 : 1);

    return newWeek;
  });
}

/**
   * Calculates an AdRun's remaining units (all units - allocated units)
   */
export function calculateRemainingUnits(adrun: AdRun): number {
  if (_.isNil(adrun)) {
    throw new Error(`Invalid value for 'adrun', got: ${adrun}`);
  }

  const allUnits = new BigNumber(adrun.units);
  const unitsAllocated = new BigNumber(adrun.weeklyBreakdown.reduce((accumulator, week) => {
    return accumulator + week.units;
  }, 0));

  return allUnits.minus(unitsAllocated).toNumber();
}



