import * as campaigns from '../actions/campaigns';
import {Campaign} from '../models/campaign';
import {BudgetType} from '../models/BudgetType';
import {ExchangeCurrency} from '../models/exchange-currency';
import {AddCampaign, FilterCampaigns, UpdateCampaign} from '../actions/campaigns';

export type State = Array<Campaign>;

const initialState: State = [];

export function reducer(state = initialState, action: campaigns.Actions): State {
  switch (action.type) {
    case campaigns.LOAD_CAMPAIGNS:
      return loadData();

    case campaigns.ADD_CAMPAIGN:
      addNewCampaign((<AddCampaign>action).payload);
      return loadData();

    case campaigns.UPDATE_CAMPAIGN:
      updateCampaign((<UpdateCampaign>action).payload);
      return loadData();

    case campaigns.FILTER_CAMPAIGNS:
      if (!(<FilterCampaigns>action).payload) {
        return loadData();
      }
      return searchCampaigns((<FilterCampaigns>action).payload);

    default:
      return state;
  }
}

