let mockDB = [...someMockData];

function loadData() {
  return mockDB;
}

function addNewCampaign(campaign: Campaign) {
  mockDB = [...mockDB, campaign];
}

function updateCampaign(editedCampaign: Campaign) {
  const otherCampaigns = mockDB.filter(campaign => campaign.identifier !== editedCampaign.identifier);
  mockDB = [editedCampaign, ...otherCampaigns];
}

function searchCampaigns(searchTerm: string) {
  const snippet = searchTerm.toLowerCase();
  return mockDB.filter(campaign =>
    campaign.campaign.toLowerCase().startsWith(snippet) ||
    campaign.client.toLowerCase().startsWith(snippet)
  );
}



