import {Action} from '@ngrx/store';
import {Campaign} from '../models/campaign';
import {AdRun} from '../models/adrun';

export const SELECT_CAMPAIGN_ADRUNS = '[ADRUNS] Select Campaign';
export const LOAD_ADRUNS = '[ADRUNS] Load';
export const ADD_ADRUN = '[ADRUNS] Add';
export const EDIT_ADRUN = '[ADRUNS] Edit';
export const DELETE_ADRUN = '[ADRUNS] Delete';
export const REORDER_ADRUNS = '[ADRUNS] Reorder';


export class SelectCampaignAdRuns implements Action {
  readonly type = SELECT_CAMPAIGN_ADRUNS;

  constructor(public campaign: Campaign, public isEditingMode?: boolean) { }
}

export class LoadAdRuns implements Action {
  readonly type = LOAD_ADRUNS;
}

export class AddAdRun implements Action {
  readonly type = ADD_ADRUN;

  constructor(public payload: AdRun) { }
}

export class EditAdRun implements Action {
  readonly type = EDIT_ADRUN;

  constructor(public payload: AdRun) { }
}

export class DeleteAdRun implements Action {
  readonly type = DELETE_ADRUN;

  constructor(public payload: AdRun) { }
}

export class ReorderAdRuns implements Action {
  readonly type = REORDER_ADRUNS;

  constructor(public payload: AdRun[]) { }
}

export type Actions = SelectCampaignAdRuns | LoadAdRuns | AddAdRun | EditAdRun | DeleteAdRun | ReorderAdRuns;



