import {Component, EventEmitter, OnDestroy, Output} from '@angular/core';
import {Subject} from 'rxjs/Subject';
import {of} from 'rxjs/observable/of';
import {map, debounceTime, distinctUntilChanged, flatMap, delay} from 'rxjs/operators';
import {Subscription} from 'rxjs/Subscription';

@Component({
  selector: 'md-campaigns-search',
  template: `
    <div class="input-group md-search">
							<span class="input-group-btn">
								<button class="fa fa-search md-main-color" type="button"></button>
							</span>
      <input type="text" class="form-control"
             placeholder="Type a campaign name to search for..."
             aria-label="Search for..."
             autofocus
             (keyup)="keyUp$.next($event)"
      >
    </div>
  `,
  styleUrls: ['./campaigns-search.css']
})
export class CampaignsSearchComponent implements OnDestroy {
  readonly DEBOUNCE_TIME = 300;

  subscription: Subscription;

  keyUp$: Subject<any> = new Subject();

  @Output() search: EventEmitter<string> = new EventEmitter();

  constructor() {
    this.subscription = this.keyUp$.pipe(
      map(event => event.target.value),
      debounceTime(this.DEBOUNCE_TIME),
      distinctUntilChanged(),
      flatMap(search => of(search).pipe(
        delay(this.DEBOUNCE_TIME)
      ))
    ).subscribe(item => {
      this.search.emit(item);
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

}



