import {Component} from '@angular/core';
import {Store} from '@ngrx/store';

import * as fromRoot from '../../reducers';
import * as fromCampaign from '../reducers';
import {State} from '../reducers/campaigns';
import {FilterCampaigns, LoadCampaigns} from '../actions/campaigns';
import {Campaign} from '../models/campaign';
import {SelectCampaignAdRuns} from '../actions/ad-runs';


@Component({
  selector: 'md-overview',
  template: `
    <md-campaigns [campaigns]="campaigns | async"
                  (selected)="selectCampaign($event)"
                  (search)="searchCampaigns($event)">
    </md-campaigns>
  `
})
export class OverviewComponent {
  public campaigns: Store<State>;

  constructor(private store: Store<fromRoot.State>) {
    // FIXME (we have this here temporarily, to load the initial data)
    this.store.dispatch(new LoadCampaigns());

    this.campaigns = store.select(fromCampaign.getCampaigns);
  }

  selectCampaign(campaign: Campaign) {
    this.store.dispatch(new SelectCampaignAdRuns(campaign));
  }

  searchCampaigns(searchQuery: string) {
    this.store.dispatch(new FilterCampaigns(searchQuery));
  }

}



