import {createFeatureSelector, createSelector} from '@ngrx/store';

import * as fromCampaigns from './campaigns';
import * as fromAdRuns from './ad-runs';

export interface State {
  entries: fromCampaigns.State;
  adRuns: fromAdRuns.State;
}

export const reducers = {
  entries: fromCampaigns.reducer,
  adRuns: fromAdRuns.reducer
};

export const getCampaignsState = createFeatureSelector<State>('campaigns');

export const getCampaigns = createSelector(
  getCampaignsState,
  (state: State) => state.entries
);

export const getSelectedCampaign = createSelector(
  getCampaignsState,
  (state: State) => state.adRuns.campaign
);

export const getAdRuns = createSelector(
  getCampaignsState,
  (state: State) => state.adRuns.campaignAdRuns
);



