import BigNumber from 'bignumber.js';
import * as _ from 'lodash';

const PERCENTAGE_TOTAL = 1;
const PERCENTAGE_DIVISOR = 100;

/**
 * Converts a percentage into a multiplier that's supposed to reduce an amount (e.g. 15% => 0.85)
 */
export function convertPercentageToReducingMultiplier(percentage: number): BigNumber {
  if (_.isNil(percentage)) {
    throw new Error(`Invalid value for 'percentage', got: ${percentage}`);
  }

  const actualPercentage = new BigNumber(percentage);
  const percentageTotal = new BigNumber(PERCENTAGE_TOTAL);
  const percentageDivisor = new BigNumber(PERCENTAGE_DIVISOR);
  const percentageNormalized = actualPercentage.dividedBy(percentageDivisor);

  return percentageTotal.minus(percentageNormalized);
}

/**
 * Converts a percentage into a multiplier that's supposed to increase an amount (e.g. 15% => 1.15)
 */
export function convertPercentageToIncreasingMultiplier(percentage: number): BigNumber {
  if (_.isNil(percentage)) {
    throw new Error(`Invalid value for 'percentage', got: ${percentage}`);
  }

  const actualPercentage = new BigNumber(percentage);
  const percentageTotal = new BigNumber(PERCENTAGE_TOTAL);
  const percentageDivisor = new BigNumber(PERCENTAGE_DIVISOR);
  const percentageNormalized = actualPercentage.dividedBy(percentageDivisor);

  return percentageTotal.plus(percentageNormalized);
}



