import {Action} from '@ngrx/store';
import {Campaign} from '../models/campaign';

export const LOAD_CAMPAIGNS = '[CAMPAIGNS] Load';
export const ADD_CAMPAIGN = '[CAMPAIGNS] Add';
export const EDIT_CAMPAIGN = '[CAMPAIGNS] Edit';
export const UPDATE_CAMPAIGN = '[CAMPAIGNS] Update';
export const FILTER_CAMPAIGNS = '[CAMPAIGNS] Filter';

export class LoadCampaigns implements Action {
  readonly type = LOAD_CAMPAIGNS;
}

export class AddCampaign implements Action {
  readonly type = ADD_CAMPAIGN;

  constructor(public payload: Campaign) { }
}

export class EditCampaign implements Action {
  readonly type = EDIT_CAMPAIGN;

  constructor(public payload: string) { }
}

export class UpdateCampaign implements Action {
  readonly type = UPDATE_CAMPAIGN;

  constructor(public payload: Campaign) { }
}

export class FilterCampaigns implements Action {
  readonly type = FILTER_CAMPAIGNS;

  constructor(public payload: string) { }
}

export type Actions = LoadCampaigns | AddCampaign | EditCampaign | UpdateCampaign | FilterCampaigns;



